                <div  className='skillSection'>
                    <div className='heading'>
                        {'Programming Languages'}
                    </div>
                    <ul>{data.programming.map(skill=>(
                        <li key={skill.name}>
                            {skill.icon&&<span><img  src={skill.icon} alt=''></img></span>}
                            {skill.name}

                        </li>
                    ))}
                    </ul>                    


                </div>
